.light-theme{


  --bgcolvar: rgba(0, 0, 0, 0.797);
  --bgbackvar:rgba(255, 255, 255, 0.771);
  
}
.dark-theme{

  --bgcolvar: rgba(255, 255, 255, 0.821);
  --bgbackvar:rgba(0, 0, 0, 0.811);
}


.backAndTheme{
  display: flex;
