  padding: 0px 15px;
}
.back-btn-icon{
  color: rgb(19, 0, 0);
  font-size: 25px;
  font-weight: bold;
  
}
.page-container {
  position: relative;
  min-height: 82vh;
}
.content-wrap {
  padding-bottom: 2.5rem;    /* Footer height */
}



