.total-item__discription{
    display: flex ;
    flex-direction: row;
    gap:1rem;
    /* align-items: flex-end; */
    flex-flow: column-reverse;
    justify-content: flex-start;
    margin: 0px 30px;
    flex: 1;
    color: rgb(241, 12, 12);
    font-size: 21px;
    
}

.total-item__price{
    color: rgb(200, 4, 4);
    padding: 0.5rem;
    border: 2px solid orange;
    background-color:  rgb(19, 0, 0);
    font-size: 18px;
    padding: 4px 12px;
    font-weight: bold;
    margin-right: 32px;
}
.total-item{
    display: flex ;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    box-shadow: 2px 1px 2px rgb(218, 229, 5);
    background-color: var(--bgbackvar);
    border: 2px solid rgb(42, 165, 91);
    margin: 15px 5px;
    padding: 10px ;
    width: 100%;
    
}

@media (max-width:500px)
{
    .total-item{
       padding:6px 6px 4px 8px;
       margin-bottom:2rem ;
       justify-content: left;
       flex-direction: row;

        
    }
    
    .total-item__price{
        color: rgb(200, 4, 4);
        padding: 0.1rem;
        font-size: 14px;
        padding: 2px 6px;
        font-weight: bold;
        margin-right: 7px;
        border-radius: 8px;
    }
    .total-item__discription{
        display: flex ;
        flex-direction: row;
        
        /* align-items: flex-end; */
        flex-flow: column-reverse;
        justify-content: flex-start;
        margin:3px 15px 2px 0px;
        color: rgb(241, 12, 12);
        font-size: 15px;
        
    }
}