

.expense-item{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    box-shadow: 2px 2px 2px rgb(19, 105, 30);
    background-color: var(--bgbackvar);
    margin: 15px 5px;
    border: 0.1px solid ;
}

.expense-item__discription{
    display: flex;
    flex-direction: row;
    gap:1rem;
    /* align-items: flex-end; */
    flex-flow: column-reverse;
    justify-content: flex-start;
    margin: 0px 20px;
    font-size: 17px;
    flex: 1;
    font-weight: bold;
    color: var(--bgcolvar);
    
}
.expense-item__price{
    color: var(--bgcolvar);
    padding: 0.5rem;
    border: 2px solid var(--bgcolvar);
    background-color: var(--bgbackvar);
    font-size: 18px;
    padding: 3px 18px;
    font-weight: bold;
    margin-right: 10px;
}
.deletebtn{
    background-color: var(--bgbackvar);
    color: red;
    font-size: 25px;
    font-weight: bold;
    border: none;
    margin: 5px;
    padding: 0px;
    cursor: pointer;
}


@media (max-width:500px)
{
    .expense-item{
       padding:1px;
       margin:1px;
       justify-content: left;
       flex-direction: row;
       margin-bottom:1rem ;
        
    }
    
    .expense-item__price{
        padding: 0.1rem;
        font-size: 13px;
        padding: 1px 3px;
        font-weight: bold;
        margin:2px 1px 5px 3px;
        border-radius: 8px;
    }
    .expense-item__discription{
        display: flex ;
        flex-direction: row;
        
        /* align-items: flex-end; */
        flex-flow: column-reverse;
        justify-content: flex-start;
        margin:2px 1px 1px 3px;
        font-size: 13px;
        
    }
    .deletebtn{
       
        font-size: 20px;
        font-weight: bold;
        border: none;
        margin: 3px;
        padding: 0px;
        padding-bottom: 4px;
        cursor: pointer;
    }
}